<template lang="pug">
include ../../../mixins/bemto/bemto
+b.neuro-button(@click.prevent="clickHandler", :class="computedClasses")
  span {{ properties.title }}
</template>

<script>
import propertiesHandler from "@/mixins/propertiesHandler";

export default {
  mixins: [propertiesHandler],
  data: function() {
    return {
      localIsActive: false
    };
  },
  props: {
    isActive: {
      type: Boolean,
      default() {
        return false;
      }
    },
    returningPressed: {
      type: Boolean,
      default() {
        return false;
      }
    },
    title: {
      type: String,
      default() {
        return "";
      }
    },
  },
  computed: {
    computedClasses() {
      const source = this.properties.returningPressed
        ? this.localIsActive
        : this.properties.isActive;
      return source ? "neuro-button--pressed" : "";
    }
  },
  methods: {
    clickHandler() {
      this.$emit("click-handler");
      this.$emit("neuro-button-handler", this.properties.title);
      this.returningPressed ? this.blinkButton() : null;
    },
    blinkButton() {
      this.localIsActive = true;
      setTimeout(() => {
        this.localIsActive = false;
      }, 50);
    }
  }
};
</script>